body, html {
    background-color: #EBECF0;
}

body, p, .Input, Select, textarea, button {
    font-family: 'Montserrat', sans-serif;
    letter-spacing: -0.2px;
    font-size: 16px;
}

div, p {
    color: #BABECC;
    text-shadow: 1px 1px 1px #FFF;
}

.Form {
    padding: 16px;
    width: 820px;
    margin: 0 auto;
}

.Segment {
    padding: 32px 0;
    text-align: center;
}

button, .Input, .Card, .Select {
    border: 0;
    outline: 0;
    font-size: 16px;
    border-radius: 320px;
    padding: 16px;
    background-color: #EBECF0;
    text-shadow: 1px 1px 0 #FFF;
}

.Card {
    border-radius: 32px;
    color: black;
}

.Card p {
    color: black;
}

label {
    display: block;
    margin-bottom: 24px !important;
    width: 100%;
}

.Input, .Card {
    margin-right: 32px;
    box-shadow: inset 2px 2px 5px #BABECC, inset -5px -5px 10px #FFF;
    width: 100%;
    box-sizing: border-box;
    transition: all 0.2s ease-in-out;
    appearance: none;
    -webkit-appearance: none;
}

.Select {
    margin-right: 32px;
    box-shadow: inset 2px 2px 5px #BABECC, inset -5px -5px 10px #FFF;
    width: 100%;
    box-sizing: border-box;
    transition: all 0.2s ease-in-out;
}

.Input:focus {
    box-shadow: inset 1px 1px 2px #BABECC, inset -1px -1px 2px #FFF;
}

button {
    color: #61677C;
    font-weight: bold;
    box-shadow: -5px -5px 20px #FFF, 5px 5px 20px #BABECC;
    transition: all 0.2s ease-in-out;
    cursor: pointer;
    font-weight: 600;
}

button:hover {
    box-shadow: -2px -2px 5px #FFF, 2px 2px 5px #BABECC;
}

button:active {
    box-shadow: inset 1px 1px 2px #BABECC, inset -1px -1px 2px #FFF;
}

button.Red {
    display: block;
    width: 100%;
    color: #AE1100;
}

button.Green {
    display: block;
    width: 100%;
    color: #4e544e;
}

label {
    margin: 0;
    flex: 1;
}